	module.exports = {"data":{"markdownRemark":{"html":"<p>YAMAHA YMF 724/744 sound card with PCM + semi-soft · semi-hard XG sound source. In 744 it became support for 4ch surround. Although I felt that the sound pressure was weak, it was around the second half of 2005 that I understood that the reason was simply that my mastering knowledge was nothing at all. Although I became a GS follower after a while for some time, I feel that the cost-effectiveness is the most cost-effective with the XG sound source I have touched. After that, Don Dona also financed living alone for PC.</p>\n<h2>Main songs made with this sound source</h2>\n<ul>\n<li>[Original] <a href=\"/will-o-the-wisp\">will-o'-the-wisp</a></li>\n<li>[Original] <a href=\"/trz-master2001\">TRZ-MASTER 2001</a></li>\n<li>[Original] <a href=\"/underground-trance\">Underground Trance</a></li>\n<li>[Rapis Lazul's BMS arrangement] <a href=\"/justice\">Justice -</a> generally <a href=\"/justice\">style</a></li>\n<li>[Original] <a href=\"/mechanism\">mechanism</a></li>\n<li>Many others</li>\n</ul>","frontmatter":{"cover":null,"date":"2000-05-01T00:00:00+09:00","strDate":"2000/4/30","title":"(2000-2004) Aopen AW 724/744"}}},"pathContext":{"path":"/en/blog/2000/05/01-aopen-aw724/","langKey":"en"}}

